import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";


//  
const init = () => {
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight,
    };

    const scene = new THREE.Scene();
    const canvas = document.querySelector('.canvas');
    const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height);
    camera.position.set(580, 520, 80);
    scene.add(camera);

    const controls = new OrbitControls(camera, canvas);
    controls.enableDamping = true;

    const renderer = new THREE.WebGLRenderer({ canvas });
    renderer.setClearColor(0x000000, 0);
    renderer.setSize(sizes.width, sizes.height);
    renderer.render(scene, camera);

    

    // 
    const lightHemisphere = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.61);
    lightHemisphere.position.set(0, 50, 0);
    scene.add(lightHemisphere);


    // 
    const loader = new GLTFLoader();

    let mixer = null;

    loader.load("model/katana.glb", (gltf) => {
        const model = gltf.scene;
        model.scale.set(500, 500, 500);

        mixer = new THREE.AnimationMixer(model);
        const action1 = mixer.clipAction(gltf.animations[0]);

        action1.setLoop(THREE.LoopOnce);
        action1.clampWhenFinished = true;

        action1.play();
        console.log(mixer)


        model.updateMatrixWorld(true);
        let bounds = new THREE.Box3().setFromObject( model );
        model.position.sub(bounds.getCenter(new THREE.Vector3()))

        scene.add(model);
    });


    //  
    const clock = new THREE.Clock();

    const animate = () => {
        const delta = clock.getDelta();

        if (mixer) {
            mixer.update(delta);
        }
        renderer.render(scene, camera);
        window.requestAnimationFrame(animate);
        controls.update();
    }

    animate();

    //      
    window.addEventListener('resize', () => {
        //  
        sizes.width = window.innerWidth;
        sizes.height = window.innerHeight;

        //    
        camera.aspect = sizes.width / sizes.height;
        camera.updateProjectionMatrix();

        //  renderer
        renderer.setSize(sizes.width, sizes.height);
        renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
        renderer.render(scene, camera);
    });

    //        
    window.addEventListener('dblclick', () => {
        if (!document.fullscreenElement) {
            canvas.requestFullscreen();
        } else {
            document.exitFullscreen();
        }
    });
};

init();


